import React, { useCallback, useEffect, useState } from 'react'
import { Card, Container, InputGroup, Row, Spinner } from 'react-bootstrap'
import { useSelector } from 'react-redux'
import { Link, useNavigate } from 'react-router-dom'
import { Button, Form } from 'react-bootstrap'
import qrCode from '../../assets/qrcodes/qrCode.png'
import Greetings from '../greetings/Greetings'
import eyeIcon from '../../assets/images/eyeIcon.png'
import closedEye from '../../assets/images/closedEye.png'
import axios from 'axios'

const Index = () => {
  const user = useSelector((state) => state.user.user)
  const [loading, setLoading] = useState(false)
  const [password, setPassword] = useState('')
  const [showPSAPassword, setShowPSAPassword] = useState(false)
  const [psaId, setPSAid] = useState('')
  const [registerationCode, setRegisterationCode] = useState('')
  const [psa, setPSA] = useState(false)
  const [changePassword, setChangePassword] = useState(false)

  const togglePasswordVisibility = () => {
    setShowPSAPassword(!showPSAPassword)
  }

  const getPsaDetails = useCallback(async () => {
    try {
      setLoading(true)
      const uniqueId = user.uniqueId
      const res = await axios.post(
        'api/v1/user/get-psa-details',
        { token: localStorage.getItem('token'), uniqueId },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem('token')}`,
          },
        },
      )
      if (res.data.success) {
        setPSAid(res.data.data.psaId)
        setRegisterationCode(res.data.data.registerCode)
        setPassword(res.data.data.psaPassword)
        setLoading(false)
        setPSA(true)
      } else {
        setLoading(false)
        setPSA(false)
      }
    } catch (error) {
      console.log(error)
      setLoading(false)
      setPSA(false)
    }
  }, [user])

  useEffect(() => {
    getPsaDetails()
  }, [getPsaDetails])
  return (
    <>
      {loading ? (
        <>
          <Container fluid className="d-flex justify-content-center align-items-center">
            <Spinner animation="border" role="status">
              <span className="visually-hidden">Loading...</span>
            </Spinner>
          </Container>
        </>
      ) : (
        <>
          <Container>
            <Greetings user={user && user} />
          </Container>

          <Container fluid>
            <Row className="align-items-center justify-ceontent-center">
              <Container className="col-12 col-md-8 col-lg-8">
                <Card
                  className=" border-1 rounded-4 border-dark"
                  style={{
                    maxWidth: '40rem',
                  }}
                >
                  <Card.Header className="text-center bg-white rounded-top-4 border-dark border-1">
                    <h5>PSA LOGIN DETAILS</h5>
                  </Card.Header>
                  <Card.Body>
                    <Form className="col-12 col-md-5">
                      <Form.Group className="mb-3 col-12">
                        <Form.Label>PSA ID</Form.Label>
                        <InputGroup>
                          <Form.Control type="text" placeholder="PSA ID" value={psaId} disabled />
                        </InputGroup>
                      </Form.Group>
                      <Form.Group className="mb-3 col-12">
                        <Form.Label>PSA Password</Form.Label>
                        <InputGroup>
                          <Form.Control
                            type={showPSAPassword ? 'text' : 'password'}
                            placeholder="Enter your password"
                            value={password}
                            onChange={(e) => {
                              setPassword(e.target.value)
                              setChangePassword(true)
                            }}
                          />

                          <Button variant="outline-secondary" onClick={togglePasswordVisibility}>
                            {showPSAPassword ? (
                              <img
                                src={eyeIcon}
                                style={{
                                  height: '1rem',
                                  objectFit: 'contain',
                                }}
                                alt="eyeIcon"
                              />
                            ) : (
                              <img
                                src={closedEye}
                                style={{
                                  height: '1rem',
                                  objectFit: 'contain',
                                }}
                                alt="eyeIcon"
                              />
                            )}
                          </Button>
                        </InputGroup>
                      </Form.Group>
                      <Form.Group className="mb-3 col-12">
                        <Form.Label>Registeration Code</Form.Label>
                        <InputGroup>
                          <Form.Control
                            type="text"
                            placeholder="Registeration Code"
                            value={registerationCode}
                            onChange={(e) => setRegisterationCode(e.target.value)}
                            disabled
                          />
                        </InputGroup>
                      </Form.Group>

                      <div className="d-flex justify-content-center">
                        <Link
                          className="myCustomBtn btn"
                          to="https://psaonline.utiitsl.com/psapanservices/forms/login.html/loginHome"
                        >
                          Login Link
                        </Link>
                      </div>
                      {!psa && (
                        <p className={`text-danger text-center mt-3`}>
                          You PSA ID is not created yet
                        </p>
                      )}
                    </Form>
                  </Card.Body>
                </Card>
              </Container>
              <Container className="col-12 col-md-4 col-lg-4">
                <img
                  src={qrCode}
                  style={{
                    height: '22rem',
                    objectFit: 'contain',
                  }}
                  alt="qrcode"
                />
              </Container>
            </Row>
          </Container>
        </>
      )}
    </>
  )
}

export default Index

